import type {ReactElement} from 'react';
import React, {useCallback, useState} from 'react';

import {PracticeRounds} from './PracticeRounds';
import {SessionEnd} from './SessionEnd';
import {SessionIntro} from './SessionIntro';
import type {SessionMetrics} from './types';
import type {Lesson} from '../Levels/types';
import type {Round} from '../Pages/assignments';

export interface PracticeSessionProps {
    session: PracticeSessionData;
}

export interface PracticeSessionData {
    lesson: Lesson;

    rounds: Round[];
}

enum PracticeSessionState {
    Intro,
    Practice,
    End,
}

export function PracticeSession({
                                    session: {lesson, rounds},
                                }: PracticeSessionProps): ReactElement {
    const [state, setState] = useState<PracticeSessionState>(PracticeSessionState.Intro);
    const [metrics, setMetrics] = useState<SessionMetrics | null>(null);
    const [blindfolded, setBlindfolded] = useState(false);

    const goBack = () => {
        history.back();
    };

    const onStart = useCallback((bf: boolean) => {
        setBlindfolded(bf);
        setState(PracticeSessionState.Practice)
    }, []);

    const handlePracticeEnd = (m: SessionMetrics) => {
        setMetrics(m);
        setState(PracticeSessionState.End);
    };

    if (state === PracticeSessionState.Practice) {
        return <PracticeRounds
            session={{lesson, rounds}}
            onQuit={goBack}
            onEnd={(m) => handlePracticeEnd(m)} />;
    }
    if (state === PracticeSessionState.End && metrics !== null) {
        return <SessionEnd lessonId={lesson.id} metrics={metrics} blindfolded={blindfolded} />;
    }

    return <SessionIntro
        tasks={lesson.tasks}
        onBack={goBack}
        onStart={onStart}
    />;
}
