import type {SxProps, Theme} from '@mui/material';
import {Box, ButtonBase, Stack} from '@mui/material';
import type {ReactElement} from 'react';
import React from 'react';

import {styles} from './styles';
import {LessonType} from '../Levels/types';
import type {Round, Task} from '../Pages/assignments';
import {TaskType} from '../Pages/assignments';

interface PracticeRoundBoardProps {
    tasks: Task[];
    hideControls: boolean;
    onAnswer: (answer: number) => Promise<void>;
    round: Round;
    totalRounds: number;
}

export function PracticeRoundBoard({
                                       hideControls,
                                       onAnswer,
                                       round,
                                       tasks,
                                       totalRounds,
                                   }: PracticeRoundBoardProps): ReactElement {
    const rightAnswer = round.tasks[round.answerIndex];
    return (
        <>
            <Stack
                sx={{
                    ...sxStyles.fullCover,
                    backgroundColor: round.lessonType == LessonType.Pick1 && rightAnswer.type === TaskType.Color
                        ? rightAnswer.value
                        : 'white',
                }}
                direction='row'
                flex={1}
                minWidth='45%'
                flexWrap='wrap'
                gap='1px'
            >
                {round.lessonType == LessonType.Pick1 && rightAnswer.type === TaskType.Shape &&
                    <Box display='flex' sx={sxStyles.fullCover} justifyContent='center'>
                        <svg width='100%' height='auto' viewBox='-100 -100 200 200'>
                            {rightAnswer.id === 'circle' &&
                                <circle cx={0} cy={0} r={80} stroke='black' strokeWidth='10' fill='transparent' />}
                            {rightAnswer.id === 'square' &&
                                <rect x={-80} y={-80} width={160} height={160} stroke='black' strokeWidth='10'
                                      fill='none' />}
                        </svg>
                    </Box>}
                {tasks.map((task, i) => (
                    <Box
                        flex={1}
                        // className={styles.optionButton}
                        position='relative'
                        key={task.id}
                    >
                        <ButtonBase
                            disabled={hideControls}
                            onClick={() => onAnswer(i)}
                            sx={{
                                ...sxStyles.fullCover,
                                backgroundColor:
                                    round.lessonType == LessonType.Pick1
                                        ? 'transparent'
                                        : task.value,
                            }}
                        />
                        {!hideControls && (
                            <Box className={styles.optionLabel}>
                                {task.label} ({i + 1})
                            </Box>
                        )}
                    </Box>
                ))}
            </Stack>
            {totalRounds > 0 &&
                <Box className={styles.roundCounter}>
                    {round.order + 1}/{totalRounds}
                </Box>}
        </>
    );
}

const sxStyles = {
    fullCover: {
        position: 'absolute',
        top: 0,
        right: 0,
        bottom: 0,
        left: 0,
    } as SxProps<Theme>,
};
