import {List} from '@mui/icons-material';
import MenuIcon from '@mui/icons-material/Menu';
import {AppBar, Box, Divider, Drawer, IconButton, ListItem, ListItemButton, ListItemText, Stack} from '@mui/material';
import type {ReactElement} from 'react';
import React, {useRef, useState} from 'react';

interface MainAppBarProps {
    absolute?: boolean;
}

export function MainAppBar({absolute = false}: MainAppBarProps): ReactElement {
    const [open, setOpen] = useState(false);
    const appBarRef = useRef<HTMLDivElement>(null);

    const handleDrawerOpen = () => {
        setOpen(true);
    };

    const handleDrawerClose = () => {
        setOpen(false);
    };

    return (<>
        <AppBar
            ref={appBarRef}
            position={absolute ? 'absolute' : 'static'}
            sx={{py: 1, px: 3, color: 'white'}}
            color='transparent'
        >
            <Stack direction='row' alignItems='center' justifyContent='space-between' gap={1}>
                <IconButton
                    color='inherit'
                    aria-label='open drawer'
                    edge='end'
                    onClick={handleDrawerOpen}
                >
                    <MenuIcon />
                </IconButton>
            </Stack>
        </AppBar>

        <Drawer
            open={open}
            onClose={handleDrawerClose}
            anchor='left'
            container={appBarRef.current}
        >
            <Box
                sx={{width: '250px'}}
                role='presentation'
                onClick={handleDrawerClose}
                onKeyDown={handleDrawerClose}
            >
                <List>
                    {['Inbox', 'Starred', 'Send email', 'Drafts'].map((text, index) => (
                        <ListItem key={text} disablePadding>
                            <ListItemButton>
                                <ListItemText primary={text} />
                            </ListItemButton>
                        </ListItem>
                    ))}
                </List>
                <Divider />
                <List>
                    {['All mail', 'Trash', 'Spam'].map((text, index) => (
                        <ListItem key={text} disablePadding>
                            <ListItemButton>
                                <ListItemText primary={text} />
                            </ListItemButton>
                        </ListItem>
                    ))}
                </List>
            </Box>
        </Drawer>
    </>);
}
