import {ArrowBack} from '@mui/icons-material';
import {Box, Checkbox, Divider, FormControlLabel, IconButton, Stack, Typography} from '@mui/material';
import React, {useEffect} from 'react';
import {Link} from 'react-router-dom';

import {useAppContext} from '../../../contexts/AppContext';
import type { UseLocalStorageResult} from '../../../utility/useLocalStorage';
import {useLocalStorage} from '../../../utility/useLocalStorage';
import {NarrowLayout} from '../../Styled/NarrowLayout';

export function Level1() {
    const {setLastLevel} = useAppContext();
    const levelState = useLocalStorage<Record<string, unknown>>('level1', {});

    useEffect(() => {
        setLastLevel(1);
    }, []);

    return <NarrowLayout>
        <IconButton component={Link} to='/'>
            <ArrowBack />
        </IconButton>
        <Typography variant='h2' gutterBottom>
            The White Blindfold
        </Typography>
        <Typography paragraph>
            The white blindfold symbolizes the first stage of the inner sight journey, serving as a visual
            representation of the student's commitment to the practice. While it is essential to have an actual
            blindfold to start practicing, the white blindfold signifies more than just a physical object. It represents
            the student's understanding that the blindfold is not a tool to restrict their vision, but rather a tool to
            develop a heightened sense of awareness and focus. The white blindfold demonstrates the student's
            willingness to embrace the practice and to cultivate the skills necessary for progression. Therefore, the
            white blindfold serves as both a symbolic and practical tool in the development of inner sight mastery.
        </Typography>

        <FormControlLabel control={<StepCheckbox dataKey="have_blindfold" levelState={levelState}/>} label='I have a blindfold to practice with.' />

        <Divider variant='middle' />

        <Typography paragraph>To start your training, you need to get comfortable with the sensation of
            wearing a blindfold. Start by wearing a blindfold for short periods of time while sitting in a quiet,
            safe place. Gradually increase the time you spend with the blindfold on. This practice will help you
            develop a sense of trust and comfort with the blindfold.</Typography>

        <Typography paragraph>As you become more comfortable with wearing a blindfold, start to incorporate mindful
            movement into your practice. This can include simple movements such as walking, stretching, or yoga
            poses. Focus on the sensations in your body and the movements you are making. This practice will help
            you develop spatial awareness and a deeper connection with your body.</Typography>

        <Typography paragraph>Feel free to use this list of ideas as a reference for familiarizing yourself with your
            blindfold.</Typography>

        <Box>
            <FormControlLabel control={<StepCheckbox dataKey="meditation" levelState={levelState}/>} label='I have meditated with a blindfold.' />
            <FormControlLabel control={<StepCheckbox dataKey="sleep" levelState={levelState}/>}
                              label='I have slept with a blindfold on (if you have a sleep mask).' />
            <FormControlLabel control={<StepCheckbox dataKey="brush_teeth" levelState={levelState}/>} label='I have brushed my teeth blindfolded.' />
            <FormControlLabel control={<StepCheckbox dataKey="eat" levelState={levelState}/>} label='I have eaten a meal blindfolded.' />
            <FormControlLabel control={<StepCheckbox dataKey="walk" levelState={levelState}/>} label='I have walked around my house/apartment blindfolded.' />
            <FormControlLabel control={<StepCheckbox dataKey="sequence" levelState={levelState}/>}
                              label='I have done all of the above in a sequence with the blindfold on.' />
            <FormControlLabel control={<StepCheckbox dataKey="yoga" levelState={levelState}/>} label='I have practiced yoga while blindfolded.' />
            <FormControlLabel control={<StepCheckbox dataKey="read_a_book" levelState={levelState}/>} label='I have read a book about seeing blindfolded.' />
            <FormControlLabel control={<StepCheckbox dataKey="convince_others" levelState={levelState}/>}
                              label='I succeeded in convincing others that seeing blindfolded is real.' />
            <FormControlLabel control={<StepCheckbox dataKey="requires_innovation" levelState={levelState}/>}
                              label='I understand that my inner vision is easily bored by routine and requires ceaseless modification, diversity, innovation and originality.' />
            <FormControlLabel control={<StepCheckbox dataKey="practice_with_friends" levelState={levelState}/>}
                              label='I understand that the ideal approach is to engage in regular practice sessions with your friends!' />

            <Typography paragraph mt={4}>Bonus for English speakers:</Typography>
            <FormControlLabel control={<StepCheckbox dataKey="joke" levelState={levelState}/>} label='I have told this joke to somebody while blindfolded:' />
            <Stack ml={2} gap={2}>
                <Box fontStyle='italic'>- Why did the blindfolded man fall into a well?<br />- Because he couldn't see
                    that well!</Box>
                {/* <Box fontStyle="italic">- Why did the blindfolded man break up with his girlfriend?<br />- Because he was seeing someone else!</Box>*/}
            </Stack>
            <Divider variant='middle' />

            <FormControlLabel control={<StepCheckbox dataKey="completed" levelState={levelState}/>}
                              label="I solemnly swear that I am up for the challenge and I'm ready to accept the symbolic white blindfold ask a mark of my journey towards mastering the inner sight." />
        </Box>
    </NarrowLayout>;
}

interface StepCheckboxProps {
    dataKey: string;
    levelState: UseLocalStorageResult<Record<string, unknown>>;
}

function StepCheckbox({levelState: [level, setLevel], dataKey}: StepCheckboxProps) {
    return <Checkbox checked={level?.[dataKey] as boolean ?? false}
                     onChange={(e) => setLevel(val => ({...val, [dataKey]: e.target.checked}))} />;
}