import {css} from '@emotion/css';
import {LoadingButton} from '@mui/lab';
import {Divider, FormControl, FormHelperText, TextField} from '@mui/material';
import React, {useCallback} from 'react';
import {useHistory, useLocation} from 'react-router-dom';

import {containers} from '../../../styles/containers';
import {typography} from '../../../styles/typography';
import {useAuth} from '../../../useAuth';

const styles = {
    formField: css`
        margin-bottom: 15px;
    `,
};

export function SignUpPage() {
    const history = useHistory();
    const location = useLocation();
    const auth = useAuth();

    const signUp = useCallback(() => {
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        const {from} = (location.state as any) || {from: {pathname: '/'}};

        auth?.signin(() => {
            history.replace(from);
        });
    }, [auth, location, history]);

    return (
        <div className={containers.narrow}>
            <h1 className={typography.appTitle}>RV-1</h1>

            <div className={containers.card}>
                <div className={styles.formField}>
                    <TextField label="E-mail" variant="outlined" fullWidth />
                </div>
                <div className={styles.formField}>
                    <TextField
                        label="Password"
                        variant="outlined"
                        type="password"
                        fullWidth
                    />
                </div>
                <div className={styles.formField}>
                    <TextField
                        label="Password Confirmation"
                        variant="outlined"
                        type="password"
                        fullWidth
                    />
                </div>
                <div className={styles.formField}>
                    <FormControl fullWidth>
                        <TextField label="Name" variant="outlined" />
                        <FormHelperText>Optional</FormHelperText>
                    </FormControl>
                </div>
                <Divider light style={{marginBottom: '1em'}} />
                <LoadingButton
                    variant="contained"
                    onClick={signUp}
                    type="button"
                    fullWidth
                >
                    Let's go!
                </LoadingButton>
            </div>
        </div>
    );
}
