import {cx} from '@emotion/css';
import {ArrowBack} from '@mui/icons-material';
import {Box, Button, IconButton, Stack, Typography} from '@mui/material';
import type {ReactElement} from 'react';
import React, {useCallback, useState} from 'react';

import {PracticeRoundBoard} from './PracticeRoundBoard';
import {styles} from './styles';
import {LessonType} from '../Levels/types';
import type {Task} from '../Pages/assignments';
import {useEventListener} from '../Pages/useEventListener';
import {NarrowLayout} from '../Styled/NarrowLayout';

interface SessionIntroProps {
    onStart: (blindfolded: boolean) => void;
    onBack: () => void;
    tasks: Task[];
}

export function SessionIntro({onStart, tasks, onBack}: SessionIntroProps): ReactElement {
    const [blindfolding, setBlindfolding] = useState(false);

    const handler = useCallback(
        () => onStart(true),
        [onStart],
    );
    useEventListener('keydown', blindfolding ? handler : undefined);

    if (blindfolding) {
        return <Box sx={[styles.fullScreen, {p: 2}]} display='flex' justifyContent='center' alignItems='center'
                    onClick={() => onStart(true)}>
            Put on your blindfold now and tap anywhere or press any key to start.
        </Box>;
    }

    return (
        <Box
            className={cx(styles.withDarkerBackground)}
            p={2}
            minHeight='100vh'
        >
            <NarrowLayout>
                <IconButton onClick={onBack}>
                    <ArrowBack />
                </IconButton>

                <Typography variant='h2' mb={2}>
                    Session prep
                </Typography>

                <Stack gap={2} mb={4}>
                    {tasks.map((t, i) => (
                        <Box position='relative' height='200px' key={t.id}>
                            <PracticeRoundBoard tasks={tasks} hideControls={false} onAnswer={() => Promise.resolve()}
                                                round={{
                                                    answerIndex: i,
                                                    tasks: tasks,
                                                    lessonType: LessonType.Pick1, // TODO: fix
                                                    order: 0,
                                                }}
                                                totalRounds={0} />
                        </Box>
                    ))}
                </Stack>
                <Stack gap={1} justifyContent='space-between' direction='row'>
                    <Button variant='outlined' fullWidth size='large'
                            onClick={() => onStart(false)}>
                        I just want to see how this works
                    </Button>
                    <Button variant='contained' fullWidth size='large'
                            onClick={() => setBlindfolding(true)}>
                        I'm ready to put my blindfold on
                    </Button>
                </Stack>
            </NarrowLayout>
        </Box>
    );
}
