import {Clear} from '@mui/icons-material';
import {Box, IconButton} from '@mui/material';
import type {ReactElement} from 'react';
import React, {useCallback, useEffect, useState} from 'react';

import {PracticeRound} from './PracticeRound';
import type {AnswerRecord, SessionMetrics} from './types';
import {playAudio} from '../../utility/playAudio';
import type {Lesson} from '../Levels/types';
import type {Round} from '../Pages/assignments';

const TOTAL_ROUNDS = 10;

export interface PracticeRoundsProps {
    session: PracticeRoundsData;
    onQuit: () => void;
    onEnd: (metrics: SessionMetrics) => void;
}

export interface PracticeRoundsData {
    lesson: Lesson;
    rounds: Round[];
}

export function PracticeRounds({
                                   session: {lesson, rounds},
                                   onQuit,
                                   onEnd,
                               }: PracticeRoundsProps): ReactElement {
    const [index, setIndex] = useState(0);
    const [roundStart, setRoundStart] = useState<Date>(new Date());
    const [sessionRecord, setSessionRecord] = useState<SessionMetrics>({
        start: new Date(),
        end: new Date(),
        answers: [],
        summary: {},
        score: 0,
    });

    useEffect(() => {
        setIndex(0);
    }, [rounds]);

    useEffect(() => {
        console.log('Round start!', new Date());
        setRoundStart(new Date());
        if (index === TOTAL_ROUNDS) {
            playAudio('sounds/congratulations.mp3');
        }

        if (index >= rounds.length) {
            onEnd(sessionRecord);
        }
    }, [index]);

    const onAnswer = (correct: boolean, givenAnswer: number, time: Date) => {
        const correctAnswer = rounds[index].answerIndex;
        const answers: AnswerRecord[] = [
            ...sessionRecord.answers,
            {
                correct,
                givenAnswer,
                start: roundStart,
                end: time,
                taskId: rounds[index].tasks[correctAnswer].id,
            },
        ];

        const {summary} = sessionRecord;
        summary[correctAnswer] = summary?.[correctAnswer] ?? {
            correct: 0,
            wrong: 0,
            task: lesson.tasks[correctAnswer],
        };
        if (correct) {
            summary[correctAnswer].correct++;
        } else {
            summary[correctAnswer].wrong++;
        }

        const correctAnswers = answers.filter(a => a.correct).length;
        const score = Math.max(correctAnswers - answers.length / 2, 0);

        console.log('OnAnswer', answers);

        setSessionRecord({...sessionRecord, answers, score});
    };

    const onNext = useCallback(() => {
        setIndex(i => i + 1);
    }, []);

    return (
        <>
            <Box position='absolute' sx={{right: 0, top: 0, zIndex: 1}}>
                <IconButton onClick={onQuit} color='primary'>
                    <Clear />
                </IconButton>
            </Box>

            <PracticeRound
                round={rounds[index]}
                totalRounds={TOTAL_ROUNDS}
                onAnswer={onAnswer}
                onNext={onNext}
            />
        </>
    );
}
