import {CssBaseline, ThemeProvider} from '@mui/material';
import type {PropsWithChildren} from 'react';
import React from 'react';
import {HashRouter, Redirect, Route, Switch} from 'react-router-dom';

import './App.css';

import {Introduction} from './Components/Levels/Introduction/Introduction';
import {LevelRoutes} from './Components/Levels/LevelRoutes';
import {BlindfoldCustomPage} from './Components/Pages/Blindfold/BlindfoldCustomPage/BlindfoldCustomPage';
import {BlindfoldSessionPage} from './Components/Pages/Blindfold/BlindfoldSessionPage/BlindfoldSessionPage';
import {HomePage} from './Components/Pages/HomePage/HomePage';
import {LearnPage} from './Components/Pages/LearnPage/LearnPage';
import {LoginPage} from './Components/Pages/LoginPage/LoginPage';
import {SignUpPage} from './Components/Pages/SignUpPage/SignUpPage';
import {PageLayout} from './Components/Styled/PageLayout';
import {AppContextProvider} from './contexts/AppContext';
import {theme} from './styles/theme';
import {ProvideAuth, useAuth} from './useAuth';

export function App() {
    return (
        <ThemeProvider theme={theme}>
            <CssBaseline />
            <ProvideAuth>
                <HashRouter>
                    <Routes />
                </HashRouter>
            </ProvideAuth>
        </ThemeProvider>
    );
}

function Routes() {
    // const location = useLocation();

    return (
        <AppContextProvider>
            <Switch>
                <Route path='/' exact>
                    <PageLayout>
                        <HomePage />
                    </PageLayout>
                </Route>
                <Route path='/level'>
                    <LevelRoutes />
                </Route>
                <Route path='/session/:id'>
                    <BlindfoldSessionPage />
                </Route>
                <Route path='/login'>
                    <LoginPage />
                </Route>
                <Route path='/custom'>
                    <BlindfoldCustomPage />
                </Route>

                <Route path='/introduction'>
                    <PageLayout>
                        <Introduction />
                    </PageLayout>
                </Route>

                <Route path='/learn'>
                    <LearnPage />
                </Route>

                <Route path='/signup'>
                    <SignUpPage />
                </Route>
            </Switch>
        </AppContextProvider>
    );
}

// A wrapper for <Route> that redirects to the login

// screen if you're not yet authenticated.
// eslint-disable-next-line @typescript-eslint/no-unused-vars
function PrivateRoute({children, ...rest}: PropsWithChildren<{}>) {
    const auth = useAuth();
    return (
        <Route
            {...rest}
            render={({location}) =>
                auth.user ? (
                    children
                ) : (
                    <Redirect
                        to={{
                            pathname: '/login',
                            state: {from: location},
                        }}
                    />
                )
            }
        />
    );
}
