import {
    Box,
    Button,
    Checkbox,
    Chip,
    FormControlLabel,
    Paper,
    Stack,
} from '@mui/material';
import React, {useState} from 'react';
import {Link} from 'react-router-dom';

import {ALL_TASKS_BY_ID} from '../../../../sessions/tasks';
import {containers} from '../../../../styles/containers';
import {PageLayout} from '../../../Styled/PageLayout';

export function BlindfoldCustomPage() {
    const [checked, setChecked] = useState<Record<string, boolean>>({});

    const isChecked = (id: string) => checked[id];

    const onChange = (id: string, value: boolean) => {
        // console.log('Change:', id, value);
        if (value) {
            setChecked({
                ...checked,
                [id]: true,
            });
        } else {
            const tmpChecked = {
                ...checked,
            };
            delete tmpChecked[id];
            setChecked(tmpChecked);
        }
    };

    const getIndex = (id: string) =>
        checked[id] ? Object.keys(checked).findIndex(v => v === id) + 1 : '';

    return (
        <PageLayout>
            <Paper className={containers.paper}>
                <Stack gap={2} alignItems="start">
                    <Box alignSelf="stretch">
                        {Object.values(ALL_TASKS_BY_ID).map(c => (
                            <Stack key={c.id} direction="row">
                                <Box
                                    alignSelf="stretch"
                                    style={{
                                        backgroundColor: c.value,
                                        width: '1em',
                                        margin: '0.2em 0.5em 0.2em 0',
                                        borderRadius: '0.3em',
                                    }}
                                />
                                <Box flex={1}>
                                    <FormControlLabel
                                        label={c.label}
                                        control={
                                            <Checkbox
                                                value={isChecked(c.id)}
                                                onChange={event =>
                                                    onChange(
                                                        c.id,
                                                        event.target.checked,
                                                    )
                                                }
                                            />
                                        }
                                    />
                                </Box>
                                <Box alignSelf="center">{getIndex(c.id)}</Box>
                            </Stack>
                        ))}
                    </Box>

                    <Stack gap={1} direction="row">
                        {Object.keys(checked).map(color => (
                            <Chip key={color} title={color} label={color} />
                        ))}
                    </Stack>
                    <Button
                        variant="contained"
                        component={Link}
                        to={
                            '/blindfold/session?tasks=' +
                            Object.keys(checked).join(',')
                        }
                    >
                        Start practice
                    </Button>
                </Stack>
            </Paper>
        </PageLayout>
    );
}
