import React from 'react';
import {Route, Switch} from 'react-router-dom';

import {Level1} from './Level1/Level1';
import {Level2} from './Level2/Level2';
import {Level3} from './Level3/Level3';
import {routeBuilder} from '../../utility/routeBuilder';
import {PageLayout} from '../Styled/PageLayout';

const levelComponents = [Level1, Level2, Level3];

export function LevelRoutes() {
    return (
        <Switch>
            {levelComponents.map((Component, index) => (
                <Route path={routeBuilder.level(index + 1)} key={index}>
                    <PageLayout>
                        <Component />
                    </PageLayout>
                </Route>
            ))}
        </Switch>
    );
}
