import {Box, ButtonBase, Stack} from '@mui/material';
import type {ReactElement} from 'react';
import React, {useMemo} from 'react';
import {Link} from 'react-router-dom';

import type {LessonConfigPick1SplitScreen} from './types';
import {LessonType} from './types';
import {LESSONS} from '../../sessions/lessons';
import {ALL_TASKS_BY_ID} from '../../sessions/tasks';
import type {Task} from '../Pages/assignments';
import {TaskType} from '../Pages/assignments';
import type {SessionMetrics} from '../PracticeSession/types';

export interface SessionButtonProps {
    lessonId: string;
}

export interface Pick1ButtonProps {
    tasks: Task[];
}

function Pick1Button({tasks}: Pick1ButtonProps) {
    return <Stack height='100%' width='100%' direction='row'>
        {tasks.map(t => (
            <Box
                flex={1}
                key={t.id}
                sx={{backgroundColor: t.type === TaskType.Color ? t.value : 'transparent'}}
            />
        ))}
    </Stack>;
}

export interface Pick1SplitScreenButtonProps {
    lesson: LessonConfigPick1SplitScreen;
    tasks: Task[];
}

function Pick1SplitScreenButton({lesson: {split}, tasks}: Pick1SplitScreenButtonProps) {
    return <Stack height='100%' width='100%' direction='column'>
        <Box flex={1} sx={{backgroundColor: tasks[0].value}} />
        <Stack flex={split - 1} direction='row'>
            {tasks.slice(1).map(t => (
                <Box
                    flex={1}
                    key={t.id}
                    sx={{backgroundColor: t.value}}
                />
            ))}
        </Stack>
    </Stack>;
}

export function LessonButton({lessonId}: SessionButtonProps): ReactElement {
    const records = useMemo<SessionMetrics[]>(() => {
        const json = localStorage.getItem(`lesson-${lessonId}.sessions`);
        return json ? JSON.parse(json) : [];
    }, [lessonId]);

    const score = records.length;

    const lesson = LESSONS[lessonId];

    const tasks = useMemo(
        () => lesson?.tasks.map(t => ALL_TASKS_BY_ID[t] as Task),
        [lessonId],
    );

    if (!lesson) {
        return <Box component='span'>Unknown lesson: {lessonId}</Box>;
    }

    return (
        <Stack direction='row' alignItems='center'>
            <Stack direction='column' alignItems='center'>
                <ButtonBase
                    component={Link}
                    to={`/session/${lessonId}`}
                    sx={styles.button}
                >
                    {lesson.type === LessonType.Pick1 && <Pick1Button tasks={tasks} />}
                    {lesson.type === LessonType.Pick1SplitScreen && (
                        <Pick1SplitScreenButton lesson={lesson} tasks={tasks} />)}
                </ButtonBase>
                <Box mt={0.5}>
                    {score > 0 ? score : '-'}
                </Box>
            </Stack>
        </Stack>
    );
}

const styles = {
    button: {
        borderRadius: '1rem',
        width: '5rem',
        height: '5rem',
        transition: 'all .3s ease-out',
        ':hover': {
            transform: 'scale(1.1)',
        },
        boxShadow: '0 3px 6px rgba(0,0,0,0.5)',
        overflow: 'hidden',
    },
};