import CheckIcon from '@mui/icons-material/Check';
import {Box, Button, CardActionArea, CardContent, Typography, useTheme} from '@mui/material';
import type {PropsWithChildren, ReactElement} from 'react';
import React from 'react';
import {Link} from 'react-router-dom';

import {useLocalStorage} from '../../utility/useLocalStorage';

export type PathStepProps = {
    title: string;
    level: number;
    disabled?: boolean;
    buttonLabel?: string;
}

const colorImages: Record<string, string> = {
    White: 'white.webp',
    Yellow: 'yellow.webp',
    Orange: 'orange.webp',
};

export function PathStep({
                             title,
                             disabled,
                             level,
                             children,
                             buttonLabel = 'Practice',
                         }: PropsWithChildren<PathStepProps>): ReactElement {
    const imageSrc = colorImages[title];
    const theme = useTheme();

    const [levelData] = useLocalStorage<Record<string, unknown>>(`level${level}`, {});
    const completed = levelData['completed'] as boolean;

    const url = `/level/${level}`;

    return <Box>
        <CardActionArea component={disabled ? 'div' : Link} to={url} sx={{
            backgroundColor: 'rgba(255,255,255,0.05)',
            borderRadius: 2,
            borderLeft: completed ? `2px solid ${theme.palette.success.dark}` : "none",
        }}>
            {completed && (<CheckIcon color='success' sx={styles.completeIcon} />)}
            <CardContent>
                <Box mb={2} textAlign='center'>
                    {imageSrc ? (
                        <img
                            src={imageSrc}
                            style={{maxWidth: '100%', maxHeight: '300px', height: 'auto'}}
                        />
                    ) : (
                        <Typography
                            gutterBottom
                            variant='h3'
                            component='div'
                            align='center'
                            mb={4}
                        >
                            {title}
                        </Typography>
                    )}
                </Box>
                <Typography variant='body1'>
                    {children}
                </Typography>
                {!disabled &&
                    <Box textAlign='center' mt={2}>
                        <Button
                            variant='contained'
                            color='primary'
                            component={Link}
                            to={url}
                        >
                            {buttonLabel}
                        </Button>
                    </Box>
                }
            </CardContent>
        </CardActionArea>
    </Box>;
}

const styles = {
    completeIcon: {
        position: 'absolute',
        top: '1rem',
        right: '1rem',
        fontSize: '3rem',
    },
};