import {ArrowBack, Menu} from '@mui/icons-material';
import {
    Box,
    Drawer,
    IconButton,
    List,
    ListItem,
    ListItemButton,
    ListItemText,
    Stack,
} from '@mui/material';
import type {ReactElement} from 'react';
import React, {useState} from 'react';
import {Link} from 'react-router-dom';

export function LearnPage(): ReactElement {
    const [menuOpen, setMenuOpen] = useState(false);

    return (
        <>
            <Stack justifyContent="space-between" direction="row">
                <IconButton component={Link} to="/home">
                    <ArrowBack />
                </IconButton>
                <IconButton onClick={() => setMenuOpen(true)}>
                    <Menu />
                </IconButton>
            </Stack>
            <Drawer
                anchor="right"
                open={menuOpen}
                onClose={() => setMenuOpen(false)}
            >
                <List>
                    {['All mail', 'Trash', 'Spam'].map((text, index) => (
                        <ListItem key={text} disablePadding>
                            <ListItemButton>
                                <ListItemText primary={text} />
                            </ListItemButton>
                        </ListItem>
                    ))}
                </List>
            </Drawer>

            <Box p={2}>
                <h1>The wall of text</h1>
                <p>
                    Everyone is born with the ability to see and read without
                    using their eyes, though some people may be better at it
                    than others. Children tend to have a natural ability to see
                    in real time without using their eyes, and can also develop
                    other gifts through practice. This practice of using their
                    third eye can help to open up their consciousness and allow
                    them to develop other gifts, depending on the individual and
                    how much time they put into practicing.
                </p>
            </Box>
        </>
    );
}
